import React, { useEffect, useState } from "react";
import { Link } from "react-scroll";
// import App from "../../App";
import "./header.css";
const Header = () => {
  const [temperature, setTemperature] = useState("");
  
  async function fetchData(city ="chennai")  {
    const url = "http://api.weatherapi.com/v1/current.json?key=d6e04fadd5384309ad090129232805&q="+city+"&aqi=no";
    try{
        const response = await fetch (url);
        const data = await response.json();
        setTemperature(`${data.current.temp_c}`);
    }catch(err){
        console.log(err);
    }
  }
  useEffect(() => {
    fetchData();
  },[]);
  
  const showtemperature=(e)=>{
     fetchData(e.target.value);
  }

    return (
        <div className="header"> 
            <div className="header_left">
                <h1> 
                    Paidi <span>Prathima</span>
                </h1>
            </div>
            <div className="header_right">
              <Link to="about" smooth={true} duration={500}>
                <h4>
                    About Me
                </h4>
              </Link>
              <Link to="skills" smooth={true} duration={500}>
                <h4>
                    Skills
                </h4>
              </Link>
              <Link to="Projects" smooth={true} duration={500}>
                <h4>
                    Projects 
                </h4>
              </Link>
              <Link to="contact" smooth={true} duration={500}>
                <h4>
                    Contact
                </h4>
              </Link>
               <h4>
                   {temperature}&deg;
               </h4>
              <select className="select_comtainer" onChange={showtemperature}>                     
                <option value="Chennai">Chennai</option>
                <option value="Coimbatore">Coimbatore</option>
                <option value="Dindigul">Dindigul</option>
                <option value="Salem">Salem</option>
                <option value="Erode">Erode</option>
                <option value="Tirunelveli"> Tirunelveli</option>
                <option value="KanniyaKumari">Kanniyakumari</option>
                <option value="Rameswaram">Rameswaram</option>
                <option value="Madurai">Madurai</option>
                <option value="Thanjavur">Thanjavur</option>
                <option value="Thoothukudi">Thoothukudi</option>
                <option value="Vellore">Vellore</option>
                <option value="Villupuram">Villupuram</option>
                <option value="Kanchipuram">Kanchipuram</option>
                <option value="Tiruvallur">Tiruvallur</option>
                <option value="Puducherry">Puducherry</option>
                <option value="Ooty">Nilgiris</option> 
                <option value="Cuddalore">Cuddalore</option>
                <option value="Nagapattinam">Nagapattinam</option>
                <option value="Trichy">Tiruchirapalli</option>
                <option value="Dharmapuri">Dharmapuri</option>
                <option value="Perambalur">Perambalur</option>
                <option value="SivaGanga">SivaGanga</option>
                <option value="Pudukottai">Pudukottai</option>
                <option value="Krishnagiri">Krishnagiri</option>
                <option value="Ariyalur">Ariyalur</option>
                <option value="Theni">Theni</option>
                <option value="Ramanathapuram">Ramanathapuram</option>
                <option value="Karur">Karur</option>
                <option value="Tiruvannamalai">Tiruvannamalai</option>
              </select>
            </div>
        </div>
    )
}
export default Header;